.formelement {
  display: block;
  width: 100%;
  box-shadow: none !important;
  color: $primary;
  border-color: $body-color;
  border-width: 1px;
  border-style: solid;
  border-radius: $border-radius;
  outline: none;
  padding: $input-btn-padding-y $input-btn-padding-x;
  appearance: none;

  &__wrapper {
    position: relative;

    &:after {
      position: absolute;
      right: $input-btn-padding-x;
      top: 50%;
      transform: translateY(-50%);
      pointer-events: none;
      filter: grayscale(1);
    }

    &--active{
      &:after{
        filter:none;
      }
    }

