      }
    }

    &--search {
      &:after {
        content: url("data:image/svg+xml,%0A%3Csvg width='17px' height='17px' viewBox='0 0 17 17' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg id='Symbols' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd' opacity='0.822470022'%3E%3Cg id='Form/Input/Text/Search/Focus' transform='translate(-421.000000, -15.000000)' fill='%230E2249' fill-rule='nonzero'%3E%3Cg id='Icon/Search' transform='translate(421.000000, 15.000000)'%3E%3Cpath d='M16.8508772,15.9594542 L12.5760234,11.6846004 C12.4998051,11.6083821 12.4003899,11.568616 12.294347,11.568616 L11.9530214,11.568616 C13.0896686,10.3391813 13.785575,8.69883041 13.785575,6.89278752 C13.785575,3.08518519 10.7003899,0 6.89278752,0 C3.08518519,0 0,3.08518519 0,6.89278752 C0,10.7003899 3.08518519,13.785575 6.89278752,13.785575 C8.69883041,13.785575 10.3391813,13.0896686 11.568616,11.9563353 L11.568616,12.294347 C11.568616,12.4003899 11.6116959,12.4998051 11.6846004,12.5760234 L15.9594542,16.8508772 C16.1152047,17.0066277 16.3670565,17.0066277 16.522807,16.8508772 L16.8508772,16.522807 C17.0066277,16.3670565 17.0066277,16.1152047 16.8508772,15.9594542 Z M6.89278752,12.7251462 C3.66842105,12.7251462 1.06042885,10.117154 1.06042885,6.89278752 C1.06042885,3.66842105 3.66842105,1.06042885 6.89278752,1.06042885 C10.117154,1.06042885 12.7251462,3.66842105 12.7251462,6.89278752 C12.7251462,10.117154 10.117154,12.7251462 6.89278752,12.7251462 Z' id='Shape'%3E%3C/path%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
      }
    }
  }

  &__options {
    display: none;
    position: absolute;
    left: 0;
    margin-top: $input-btn-padding-y;
    border: $border-width solid $body-color;
    width: 100%;
    border-radius: $border-radius;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    border-top-width: 0;
    background: $white;
    z-index:999;
  }

  &__option {
    &:not(.formelement__option--selected) {
      padding: $input-btn-padding-y $input-btn-padding-x;
      transition: 0.3s;
      cursor: pointer;

      &:hover {
        background-color: rgba($blue, 0.1);
      }
    }
  }

  &--select {
    &.formelement {
      &--active {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
        border-color: $body-color !important;
        border-width: $border-width !important;
        border-bottom-color: transparent !important;

        .formelement {
          &__options {
            display: block;
          }
        }
      }
    }
  }

  &--input {
    &:focus {
      color: $primary;
      border-color: $primary;

      + .formelement__label {
        display: block;
      }
    }
  }

  &--select,
  &--search {
    padding-right: calc(#{$input-btn-padding-x} * 2);
  }

  &:placeholder-shown:not(:active):not(:focus),
  &--placeholder {
    background-color: rgba($gray, 0.35);
    border-color: transparent;

    &:hover {
      border-color: $body-color;
    }

    + .formelement__label {
      display: none;
    }
  }

  &::placeholder,
  &__option--placeholder {
    text-transform: uppercase;
    color: $body-color;
    letter-spacing: 1.75px;
  }

  &__label {
    position: absolute;
    text-transform: uppercase;
    letter-spacing: 1.38px;
    font-size: $font-size-xs;
    top: 0;
    transform: translateY(-50%);
    background: $white;
    left: calc(#{$input-btn-padding-x} - 0.5rem);
    padding: 0 0.5rem;
    line-height: 1;
  }
}
